.add-btn {
  width: 3rem;
  height: 3rem;
  padding: 0;

  border-radius: 50%;
  border: none;

  color: #fff;
  font-size: 1.8rem;

  background-color: #007bff;

  &:hover {
    background-color: darken(#007bff, 5);
  }
}

.form {
  input, select, button, label {
    font-size: 1.6rem;
  }
}